
* {margin: 0; padding: 0
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  /* margin-top: 60px; */

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.site-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-width: 200px;
  max-width: 980px;
  width: 100%;
  margin: 0 auto;
  padding: 45px;
}

body {
  background: #e2e2e2;
}
.header {
  height: 40px;
  line-height: 40px;
  background: #fff;
  margin-bottom: 20px;
}
.header .nav {
    float: right;
    margin-right: 50px;
}
.header .nav a {
      color: #556;
      font-size: 18px;
      font-weight: 500;
      text-decoration: none;
      margin-right: 5px;
}

.footer {
  text-align: center;
  padding: 10px;
}

.main {
  background: #fff;
}
.main .item {
    padding-bottom: 5px;
}
.main .item .title > a {
      color: #778087;
      text-decoration: none;
}
.main .item .date {
      float: right;
      color: #778087;
}

.about {
  margin: 0 auto;
  padding: 45px;
  background: #fff;
  text-align: center;
}
.about .contact {
    margin-top: 15px;
}
.about .contact img {
      width: 32px;
      height: 32px;
}
.about .contact img:hover {
        opacity: 0.5;
}

@media (max-width: 767px) {
.markdown-body {
    padding: 15px;
}
}
